import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./styles2.css";

export default function Faculties() {
  let [faculties, setFaculties] = useState([]);
  var experience = 0;

  useEffect(() => {
    fetch("https://62db8e08d1d97b9e0c509966.mockapi.io/faculties")
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setFaculties(res);
      });
  }, []);
  const cardFaculties = faculties.map((fac) => {
    return (
      <>
        <div class="col-md-3">
          <br />
          <div class="card" style={{ width: "18rem", marginLeft: "10px" }}>
            <img
              class="card-img-top"
              src={fac.FacultyImage}
              alt="Card image cap"
            />
            <div class="card-body">
              <b class="card-text">{fac.FacultyName}</b>
              <br />
              <span style={{ color: "crimson" }}>{fac.FacultyDesignation}</span>
              <hr />
              <span style={{ color: "gray" }}>
                {fac.FacultyHighestEducation}
              </span>
              <hr />
              <table>
                <tr>
                  <td style={{ textAlign: "center" }}>
                    &nbsp;&nbsp;Experience&nbsp;&nbsp;
                  </td>
                  <td style={{ textAlign: "center" }}>
                    &nbsp;&nbsp;Working Since&nbsp;&nbsp;
                  </td>
                </tr>
                <tr>
                  <td>
                    <div
                      style={{
                        color: "blue",
                        backgroundColor: "lightblue",
                        textAlign: "center",
                        borderRadius: "10%"
                      }}
                    >
                      {(fac.FacultyExperienceInYears % 20) + "+ Years"}
                    </div>
                  </td>
                  <td>
                    <div
                      style={{
                        color: "grey",
                        backgroundColor: "lightgrey",
                        textAlign: "center",
                        borderRadius: "10%"
                      }}
                    >
                      {fac.FacultyWorkingSince.split("T")[0]}
                    </div>
                  </td>
                </tr>
              </table>
              <br />
              <div style={{ textAlign: "center" }}>
                <Link to={"../faculty/" + fac.id}>
                  <button class="learn-more">
                    <span class="circle" aria-hidden="true">
                      <span class="icon arrow"></span>
                    </span>
                    <span class="button-text">View Profile</span>
                  </button>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  });
  return (
    <div align="center">
      <div class="row">{cardFaculties}</div>
      <br />
    </div>
  );
}
