import logo from "./react-native-UX-design.gif";
import "./style4.css";
export default function Home() {
  return (
    <div class="row maindivv">
      <div class="col-md-6 coll1">
        <h1>Welcome to the Faculty Management system of Random University.</h1>
        <br />
        <button
          type="button"
          class="btn btn-dark btn-lg"
          style={{ marginRight: "20px" }}
        >
          <i class="fab fa-apple"></i>
          <br />
          Download
        </button>
        <button type="button" class="btn btn-outline-light btn-lg">
          <i class="fab fa-google-play"></i>
          <br />
          Download
        </button>
      </div>
      <div class="col-md-6 coll2">
        <img class="img-fluid" src={logo} alt=".." width="100%" />
      </div>
    </div>
  );
}
