import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import "./style3.css";

export default function FacultyDetail() {
  let params = useParams();
  const [faculty, setFaculty] = useState({});

  useEffect(() => {
    fetch("https://62db8e08d1d97b9e0c509966.mockapi.io/faculties/" + params.id)
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setFaculty(res);
      });
  }, []);
  return (
    <>
      <div class="container">
        <br />
        <div class="row">
          <div class="col-md-4">
            <div class="card" style={{ width: "18rem", marginLeft: "10px" }}>
              <img
                class="card-img-top"
                src={faculty.FacultyImage}
                alt="Card image cap"
              />
              <div class="card-body" style={{ textAlign: "center" }}>
                <b class="card-text">{faculty.FacultyName}</b>
                <br />
                <span style={{ color: "crimson" }}>
                  {faculty.FacultyDesignation}
                </span>
                <hr />
                <span style={{ color: "gray" }}>
                  {faculty.FacultyHighestEducation}
                </span>
                <hr />
                <table>
                  <tr>
                    <td style={{ textAlign: "center" }}>
                      &nbsp;&nbsp;Experience&nbsp;&nbsp;
                    </td>
                    <td style={{ textAlign: "center" }}>
                      &nbsp;&nbsp;Working Since&nbsp;&nbsp;
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div
                        style={{
                          color: "blue",
                          backgroundColor: "lightblue",
                          textAlign: "center",
                          borderRadius: "10%"
                        }}
                      >
                        {(faculty.FacultyExperienceInYears % 20) + "+ Years"}
                      </div>
                    </td>
                    <td>
                      <div
                        style={{
                          color: "grey",
                          backgroundColor: "lightgrey",
                          textAlign: "center",
                          borderRadius: "10%"
                        }}
                      >
                        {parseInt(faculty.FacultyWorkingSince) - 0}
                      </div>
                    </td>
                  </tr>
                </table>
                <hr />
                <table>
                  <tr>
                    <td>
                      <i class="fas fa-phone me-3"></i>
                    </td>
                    <td>
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      {faculty.FacultyMobileNumber}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <i class="fas fa-envelope me-3"></i>
                    </td>
                    <td>
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      {faculty.FacultyEmailAddress}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <i class="fas fa-map-marker me-3" aria-hidden="true"></i>
                    </td>
                    <td>
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      {faculty.FacultySeating}
                    </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
          <div class="col-md-8">
            <div>
              <br />
              <br />
              <h3 style={{ color: "crimson" }}>
                <u>Profile</u>:
              </h3>
              <p>{faculty.FacultyProfileDescription}</p>
            </div>
            <hr />
            <div class="row mb-3">
              <div class="col-5 mr-3">
                <h5 class="text-dark ">Area of Specialization</h5>
              </div>
              <div class="col">
                <h5 class="text-muted">{faculty.FacultyAreaSpecialization}</h5>
              </div>
            </div>
            <div class="row">
              <div class="col-5 mr-3">
                <h5 class="text-dark">Subjects Taught</h5>
              </div>
              <div class="col">
                <h5 class="text-muted">{faculty.FacultySubjectsTaught}</h5>
              </div>
            </div>
            <br />
            <br />
            <Link to="../../faculties/">
              <button class="hara"> &lt; GoBack </button>
            </Link>
          </div>
        </div>
      </div>
      <br />
    </>
  );
}
